import React, { useState, useEffect } from "react";
import uzbegimService from "../../services/uzbegim-service";
import Loader from "../loader/loader";
import MenuCard from "../menuCard/menuCard";
import Tabs from "../tabs/tabs";

import DishInfo from "../dishInfo/dishInfo";

const server = new uzbegimService();

const MenuBlock = () => {
  const [products, setProducts] = useState(null);
  const [info, setInfo] = useState(null);

  useEffect(() => {
    getProducts();

    return () => {};
  }, []);

  const getProducts = async () => {
    try {
      const data = await server.getMenu();
      setProducts(data);
    } catch (err) {
      console.log(err);
    }
  };

  const getDishInfo = async (chosenSlug) => {
    try {
      const data = await server.getInfo(chosenSlug);
      setInfo(data);
    } catch (err) {
      console.log(err);
    }
  };

  const openInfo = (e) => {
    const chosenSlug = e.target.getAttribute("data-course");
    getDishInfo(chosenSlug);
  };

  const onTabClick = (e) => {
    const clickedTab = e.target;
    const value = clickedTab.getAttribute("data-category").toString();
    const foodTitle = Array.from(document.querySelectorAll(".category-title"));

    foodTitle.forEach((el) => {
      if (value === el.getAttribute("data-title-category")) {
        el.scrollIntoView({ behavior: "smooth" });
      }
    });
  };

  let tabsMount = () => {
    const tabs = products.map((el, index) => (
      <Tabs key={index} category={el._id} onTabchange={onTabClick} />
    ));

    return tabs;
  };

  let cardsMount = () => {
    const menu = products.map((el, index) => {
      return (
        <div key={index}>
          <h2 data-title-category={el._id} className="category-title">
            {el._id}
          </h2>
          <MenuCard menu={el} openInfo={openInfo} />
        </div>
      );
    });

    return menu;
  };

  if (!products) {
    return <Loader />;
  } else {
    return (
      <div>
        <div className="tabs-wrapper">{tabsMount()}</div>
        <div>{cardsMount()}</div>
        <DishInfo
          info={info}
          closeInfo={() => {
            setInfo(null);
          }}
        />
      </div>
    );
  }
};

export default MenuBlock;
